.leaflet-popup-content-wrapper {
  border-radius: 0;

  > .leaflet-popup-content {
    line-height: 1.6;

    .c-map-popup {

      .popup-header {
        padding: 0 0 #{$space-1 / 2};
        border-bottom: 1px solid palette(gray, lightest);
        > .layer-name {
          font-size: $font-size-default;
          font-weight: 700;
        }
      }

      .popup-content {
        margin: $space-1 0 0 0;

        > .warning {
          > .run-analysis-btn {
            color: palette(blue, secondary);
            text-decoration: underline;
          }
        }
      }

      .popup-table {

        .dc {
          vertical-align: initial;
        }
        .dt {
          font-weight: 700;
        }
        .dd {
          padding: 0 0 0 $space-1;
        }
      }

      .popup-footer {
        margin: $space-1 0 0;
        padding: #{$space-1 / 2} 0 0;
        border-top: 1px solid palette(gray, lightest);

        > .btn-container {
          display: flex;
          justify-content: flex-end;

          > .remove-point-btn {
            font-size: $font-size-default;
            color: palette(blue, secondary);
            text-decoration: underline;
          }
        }
      }
    }
  }
}